<template>
  <div class="all-user">
    <!--  -->
    <el-form  inline label-position="right" label-width="100px">
      <el-row>
        <el-form-item label="">
          <el-input v-model.trim='searchParams.name' @keyup.enter.native='searchList' size="small" clearable></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select size="small" v-model="searchParams.couponType" @keyup.enter.native='searchList' placeholder="" clearable>
            <el-option label='' value=''></el-option>
            <el-option v-for="(item,index) in couponTypes" :key="index" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item v-if="couponData==='shop'" label="">
          <el-select size="small" v-model="value1" placeholder="">
            <el-option v-for="(item,index) in shopList" :key="index" :label="item.name" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-button @click="searchList" class="ml40 mb20 mt4"  type="primary" size="small">
        </el-button>
        <el-button @click="showEdit({},0)"  class="ml40 mb20" type='primary' size='small'></el-button>
      </el-row>
    </el-form>
    <!--  -->
    <el-table :data="couponList" border stripe class="mb20" v-loading="loading">
      <el-table-column label="">
        <template slot-scope='{row}'>
          <span>{{row.couponType | couponType}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="name" label="" width='200px'></el-table-column>
      <el-table-column prop="money" label="">
        <template slot-scope='{row}'>
          <span>{{($math.divide(row.money,100))}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width='170px'>
        <template slot-scope='scope'>
          <span>{{scope.row.startTime | formatDate}} ~ {{scope.row.endTime | formatDate}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width='170px'>
        <template slot-scope='{row}'>
          <span>{{row.createTime | formatDate}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="totalNumber" label=""></el-table-column>
      <el-table-column prop="receivedNumber" label=""></el-table-column>
      <el-table-column prop="usedNumber" label=""></el-table-column>
      <!-- <el-table-column prop="id" label="" v-if="couponData==='shop'"></el-table-column> -->
      <el-table-column prop="id" label="" width="380px">
        <template slot-scope="scope">
          <el-button v-if='!scope.row.receivedNumber' icon="el-icon-edit" type="primary" size="small" @click="showEdit(scope.row,1)"></el-button><!---->
          <el-button icon="el-icon-view" type="success" size="small" @click="showDetail(scope.row)"></el-button>
          <!-- <el-button v-if='!scope.row.receivedNumber || (scope.row.receivedNumber && !scope.row.isPast)' 
          icon="el-icon-share" type="success" class="mt10" @click="shareCoupon(scope.row)" size="small"></el-button>  -->
          <el-button v-if='!scope.row.receivedNumber' icon="el-icon-delete" type="danger" size="small" @click="deleteCoupon(scope.row)"></el-button> <!---->
        </template>
      </el-table-column>
    </el-table>
    <!---->
    <pagination :currentPage="page.page" :pageSize="page.limit" :total="page.total" @change="changePage"></pagination>
    <!--  -->
    <editCoupon :couponObj='couponObj' @closemain='closemain' @refresh="searchList"/>
    <!--  -->
    <couponDetail :detailObj='detailObj'/>
    <!--  -->
    <el-dialog id='code-div' class="tc" :visible.sync="isShowCode" :title="codeTitle+ ' '" width="400px" :close-on-click-modal='false'>
      <qrcodeVue class="mb20" :value="codeValue" :size="size" level="H"/>
      <el-button type='primary' class="mt20" @click='downCode' icon='el-icon-download' plain></el-button>
    </el-dialog>
  </div>
</template>
<script>
import editCoupon from '@/components/couponManage/editCoupon';
import couponDetail from '@/components/couponManage/couponDetail';
import qrcodeVue from 'qrcode.vue';
import {couponType} from '@/config'
import {getCouponList,delCoupon} from '@/api/order'
  export default {
    props:{
      couponData:{
        type:String,
        default:'hbb'
      },
    },
    components:{
      editCoupon,
      couponDetail,
      qrcodeVue
    },
    data() {
      return {
        loading:false,
        searchParams:{},
        couponList: [],
        // 
        page: {
          page: 1,
          limit: 10,
          total: 0
        },
        couponObj:{
          isShow:false,
          isEdit:false,
          data:{}
        },
        detailObj:{
          isShow:false,
          data:{}
        },
        couponTypes: couponType,
        shopList: [],
        codeTitle:'',
        isShowCode:false,
        codeValue:'',
        size:200,
      }
    },
    methods: {
      //
      searchList() {
        let param = {
          ... this.searchParams,
          page: this.page.page,
          pageSize: this.page.limit
        }
        this.loading=true;
        getCouponList(param).then(res => {
          this.loading=false;
          if(res.code === 200){
            this.couponList = res.data.results
            this.page.total = res.data.count
          }
        }).catch(err=>{
          this.loading=false;
        })
      },
      //
      showEdit(row,type){
        this.couponObj = {
          isShow: true,
          isEdit: !!type,
          data: !!type ? row : {}
        }
      },
      //
      showDetail(row){
        this.detailObj = {
          isShow: true,
          data: row
        }
      },
      closemain(data){
        this.couponObj.isShow = false
      },
      //
      shareCoupon(row){
        this.codeTitle = '' + row.name + ''
        this.isShowCode = true
        this.codeValue = row.uuid.toString() //inviteUrl + '/' + row.id
      },
      //
      deleteCoupon(row){
        this.$confirm(''+ row.name + '?','',{
          confirmButtonText:'',
          cancelButtonText:'',
          type: 'warning'
        }).then(()=>{
          delCoupon({uuid:row.uuid}).then(res => {
            if(res.code === 200){
              this.$message.success(res.msg || '')
              this.searchList()
            }
          })
        }).catch(()=>{
          this.$message.info('')
        })
        
      },
      //
      downCode(){
        //canvas
        let myCanvas = document.getElementById('code-div').getElementsByTagName('canvas');
        //a
        let a = document.createElement("a")
        //ahrefcanvaspng
        a.href = myCanvas[0].toDataURL('image/png').replace('image/png', 'image/octet-stream')
        //
        a.download = this.codeTitle + '.jpg'
        //
        a.click()
      },
      changePage(page, pageSize) {
        this.page.limit = pageSize;
        this.page.page = page;
        this.searchList();
      },
      openChapterManage() {
        this.chapterManageDia = true;
      }

    },
    mounted(){
      this.searchList()
    }
  }
</script>
<style lang="less" scoped>
  .allClass {
    span {
      margin-right: 20px;
      cursor: pointer;
    }

    .typeActived {
      color: #409EFF;
    }

    margin-bottom:25px;
  }
</style>