<template>
  <div class="coupon-detail">
    <el-dialog :visible.sync='detailObj.isShow' title='' width='900px' :close-on-click-modal='false'>
      <el-form label-position="right" label-width="100px">
        <el-row>
          <el-col :span='12'>
            <el-form-item label="">
              <span>{{detail.source===1?'':''}}</span>
              <!-- <span>xx</span> -->
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="">
              <span>{{detail.couponType | couponType}}</span>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="">
              <span>{{ $math.divide(detail.money,100) }} </span>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="">
              <span>{{detail.totalNumber || 0}} </span>
            </el-form-item>
          </el-col>
        </el-row>
          <el-form-item label="">
            <span>{{detail.name}}</span>
          </el-form-item>
          <el-form-item label="" v-if="[2,3].includes(detail.couponType)">
            <span> {{$math.divide(detail.accountMoney,100)}} <span class="col-danger">{{!detail.isPromotion?'':''}}</span></span>
          </el-form-item>
          <el-form-item label="">
            <span>{{detail.startTime | formatDate}}  {{detail.endTime | formatDate}}</span>
          </el-form-item>
      </el-form>
      <!--  -->
      <el-table v-if='detail.couponType===1' :data="goodsInfo" border stripe class="mb20">
        <el-table-column label="/">
          <template slot-scope='{row}'>
            <el-row>{{row.name}}</el-row>
            <el-row><img class="avatar" :src="row.goodsImg" alt="" style="margin: 0 auto;"></el-row>
          </template>
        </el-table-column>
        <el-table-column prop="goodsType" label=""></el-table-column>
        <el-table-column prop="id" label="">
          <template slot-scope='{row}'>
            <el-row>{{ $math.divide(row.realPrice,100) }}   </el-row>
            <el-row><del>{{ $math.divide(row.originalPrice,100) }}  </del></el-row>
          </template>
        </el-table-column>
        <el-table-column prop="salesNum" label=""></el-table-column>
      </el-table>
      <el-row class="info-div">
        <span class="bold"></span>
        <span class="ml20 mr20">{{detail.receivedNumber || 0}} </span>
        <span>{{detail.usedNumber || 0}} </span>
      </el-row>
      <div class="mt20">
        <el-form inline>
          <el-form-item label="">
            <el-select size="small" v-model="searchParams.status" placeholder="">
              <el-option v-for="(item,index) in useStatus" :key="index" :label="item.name" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-date-picker v-model="time" type="daterange" range-separator="" start-placeholder=""
              end-placeholder="" value-format='yyyy-MM-dd' size="small"></el-date-picker>
          </el-form-item>
          <el-button @click="searchList" class="mb20" type="primary" size="small"></el-button>
        </el-form>
      </div>
      <el-table :data="recordList" border stripe class="mb20" v-loading="loading">
        <el-table-column prop="couponsUuid" label="" width='260px'></el-table-column>
        <el-table-column label="">
          <template slot-scope='{row}'>
            <el-row>{{row.nickName}}</el-row>
            <el-row>{{row.tel}}</el-row>
          </template>
        </el-table-column>
        <el-table-column label="" width='170px'>
          <template slot-scope='{row}'>
            <span>{{row.createTime | formatDate}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width='250px'>
          <template slot-scope='{row}'>
            <div v-if='row.status===2'>
              <el-row class='col-danger'></el-row>
              <el-row>:{{row.orderInfo.usedTime | formatDate}}</el-row>
              <el-row>:{{row.orderInfo.orderNum}}</el-row>
            </div>
            <div class="col-sucs" v-if='row.status===1'></div>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <pagination :currentPage="page.page" :pageSize="page.limit" :total="page.total" @change="changePage"></pagination>
      <!--  -->
    </el-dialog>
  </div>
</template>
<script>
  import {sendCpnRecord} from '@/api/order'
  export default {
    props:{
      detailObj:{
        type:Object,
        default:()=>{return {}}
      },
    },
    data() {
      return {
        searchParams:{},
        goodsInfo:[],
        recordList: [],
        // 
        page: {
          page: 1,
          limit: 10,
          total: 0
        },
        loading:false,
        time:[],
        detail:{},
        useStatus:[
          { name: "", value: "" },
          { name: "", value: "1" },
          { name: "", value: "2" },
        ],
        shopList: [],
      }
    },
    methods: {
      //
      searchList() {

        let time = this.time || []
        time = this.$utils.formatRange(time)
        let param = {
          status: this.searchParams.status,
          page:this.page.page,
          pageSize:this.page.limit,
          couponsUuid: this.detail.uuid,
          searchTime_before: time[0],
          searchTime_after: time[1]
        }
        this.loading=true;
        sendCpnRecord(param).then(res => {
          this.loading=false;
          if(res.code === 200){
            this.recordList = res.data.results
            this.page.total = res.data.count
          }
        }).catch(err=>{
          this.loading=false;
        })
      },
      showEdit(row){
        this.couponObj = {
          isShow: true,
          isEdit: true,
          data: row
        }
      },
      changePage(page, pageSize) {
        this.page.limit = pageSize;
        this.page.page = page;
        this.searchList();
      },
      openChapterManage() {
        this.chapterManageDia = true;
      }

    },
    mounted(){
    },
    computed:{
      isShow(){
        return this.detailObj.isShow
      }
    },
    watch:{
      isShow(a){
        if(a){
          this.detail = {...this.detailObj.data}
          //
          if(this.detail.couponType === 1 && this.detail.goodsInfo !==null){
            this.goodsInfo=[];
            this.goodsInfo.push({
              name:this.detail.goodsInfo.name || '',
              realPrice:this.detail.goodsInfo.realPrice || '',
              originalPrice:this.detail.goodsInfo.originalPrice || '',
              goodsType:this.detail.goodsInfo.goodsType || '',
              goodsImg:this.detail.goodsInfo.goodsImg || '',
              salesNum:this.detail.goodsInfo.salesNum || '',
            })
          }
          this.searchList()
        }
      }
    }
  }
</script>
<style lang="less" scoped>
  /deep/.el-dialog{
    margin-top: 5vh !important;
  }
  .el-form-item {
    margin-bottom: 0px;
  }
  /deep/ .el-form-item__label,
  /deep/ .el-form-item__content{
    line-height: 30px;
  }
</style>